import { useWeb3React } from "@web3-react/core";
import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { Approve } from "../../components/Approve";
import { Mint } from "../../components/Mint";
import { Buy } from "../../components/Buy";
import { Sell } from "../../components/Sell";
import { BalancePrices } from "../../components/BalancePrices";
import TradeContext from "../../contexts/TradeContext";
import { TradeInfo } from "../../dtos/TradeInfo";
import { TradingLog } from "../../components/TradingLog";
import { TradeInfoService } from "../../services/TradeInfoService";
//import { CountdownRenderer } from "../../components/CountdownRenderer";
//import { generationStartDate } from "../../constants";


const Wrapper = styled.div`
  padding: 1em;
  display: grid;
  grid-gap: 1.5em;
  max-width: 50em;
  grid-template-columns: auto auto;

  ${({ theme }) => theme.mediaWidth.upToSmall`
    max-width: 100%;
    font-size: 0.75em;
 `};
`;


const ControlsWrapper = styled.div`
  display: grid;
  grid-gap: 1.5em;
`;


export const Main = () => {
  const { account, library, chainId } = useWeb3React();
  const [trades, setTrades] = useState<TradeInfo[]>([]);

  useEffect(() => {
    const service = new TradeInfoService(library, account!);
    service.getTradeInfo("").then(addTradeInfo);
  }, [account, chainId])

  const addTradeInfo = (trade: TradeInfo) => {
    setTrades((trades) => [...trades, trade])
  }

  return (
    <Wrapper>
      {account && chainId && library
        ? <TradeContext.Provider value={{ addTradeInfo, trades }}>
          <ControlsWrapper>
            <Buy />
            <Sell />
            <BalancePrices />
            <Approve />
            <Mint />
          </ControlsWrapper>
          <TradingLog />
        </TradeContext.Provider>

        : null}
    </Wrapper>
  );
};
