import React, { useState } from 'react';
import styled from 'styled-components';
import { Chain, SUPPORTED_NETWORKS } from '../../constants';
import { ButtonPrimary } from "../Button/index";
import Loader from '../Loader'

const StyledLoader = styled(Loader)`
  margin-right: 1rem;
`
declare const window: any

export default function SwitchNetwork() {
  const [loading, setLoading] = useState<boolean>(false)

  const addNetwork = (params: any) => {
    setLoading(true)
    window.ethereum.request({ method: 'wallet_addEthereumChain', params })
      .then(() => {
        setLoading(false)
      })
      .catch((error: Error) => console.log(`Error: ${error.message}`))
  }
  return (
    <ButtonPrimary onClick={() => addNetwork([SUPPORTED_NETWORKS[Chain.BSCTestnet]])} >
      {loading
        ? <><StyledLoader stroke="#FFF" />Switching Networks...</>
        : 'Switch to BSC Testnet'
      }
    </ButtonPrimary>
  )
}
